@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;600;700;900&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{max-height:150px;max-width:300px;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin 1 2s ease-out;animation:App-logo-spin 1 2s ease-out}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{0%{-webkit-transform:translate(0, -40vh);transform:translate(0, -40vh)}25%{-webkit-transform:translate(0, -30vh);transform:translate(0, -30vh)}50%{-webkit-transform:translate(0, -20vh);transform:translate(0, -20vh)}75%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}80%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}85%{-webkit-transform:translate(0, 7px);transform:translate(0, 7px)}90%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}95%{-webkit-transform:translate(0, 3px);transform:translate(0, 3px)}100%{-webkit-transform:translate(0);transform:translate(0)}}@keyframes App-logo-spin{0%{-webkit-transform:translate(0, -40vh);transform:translate(0, -40vh)}25%{-webkit-transform:translate(0, -30vh);transform:translate(0, -30vh)}50%{-webkit-transform:translate(0, -20vh);transform:translate(0, -20vh)}75%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}80%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}85%{-webkit-transform:translate(0, 7px);transform:translate(0, 7px)}90%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}95%{-webkit-transform:translate(0, 3px);transform:translate(0, 3px)}100%{-webkit-transform:translate(0);transform:translate(0)}}@media (min-width: 768px){.bd-placeholder-img-lg{font-size:3.5rem}}li{list-style:none}a,button{color:#014f52 !important;text-decoration:none}.search{position:relative}.search svg{position:absolute;top:22px;right:36px}.search .spartan-txt{color:#014f52 !important;font-family:'Spartan';font-weight:500}.text-dark{color:#014f52 !important}.bg-image{position:fixed;z-index:-1}h3.spartan-txt{font-family:'Spartan';font-weight:700}p.spartan-txt{font-family:'Spartan';font-weight:500}

